document.getElementById('button').addEventListener('click', function () {
  var parrafo = document.getElementById('parrafoOculto');

  if (parrafo.style.display === 'none') {
    parrafo.style.display = 'block';
  } else {
    parrafo.style.display = 'none';
  }
});

